*{box-sizing:border-box;margin:0;padding:0;font-family:"Roboto",sans-serif;-webkit-user-select:none;-ms-user-select:none;user-select:none;scroll-behavior:smooth}html{background:#000;height:100%}body{min-height:100vh;position:relative;background:#262a59;color:#fff;height:100%}#root{height:100%}@media all and (min-width: 1400px) and (max-width: 1536px) and (max-height: 864px){html{font-size:12px}}@media all and (min-width: 940px) and (max-width: 1399px) and (max-height: 768px) and (orientation: landscape){html{font-size:10px}}@media all and (min-width: 481px) and (max-width: 939px) and (max-height: 768px) and (orientation: landscape){html{font-size:8px}}@media screen and (max-width: 480px) and (orientation: portrait){html{font-size:5px}}image{max-width:100%;height:auto}.App{height:100%;display:flex;justify-content:center;align-items:center}.App main{height:100%;display:grid;justify-content:center;align-items:center}

.current-weather-container{width:100%;height:21.5rem;display:grid;grid-template-columns:1fr 3fr 1fr 4fr 1fr 5fr 1fr;grid-template-areas:"spc icn spc tmp spc inf spc";grid-column-gap:1rem;-webkit-column-gap:1rem;column-gap:1rem;padding:0 1rem 0 1rem}.current-weather-container__icon-container{grid-area:"icn";display:flex;justify-content:center;align-items:center}.current-weather-container__icon{fill:#ffc700;width:100%;height:auto}.current-weather-container__temperature{grid-area:"tmp";display:grid;grid-template-columns:repeat(2, 2fr);grid-template-rows:3.5rem 16rem;grid-template-areas:"cnd max-min" "crnt crnt";grid-gap:2rem;gap:2rem}.current-weather-container__condition{grid-area:cnd;justify-self:left;height:100%;font-family:"Roboto",sans-serif;font-style:normal;font-weight:normal;font-size:2.5rem;line-height:3rem;color:#a8aabd}.current-weather-container__max-min{grid-area:max-min;justify-self:right;font-family:"Roboto",sans-serif;font-style:normal;font-weight:normal;font-size:2.5rem;line-height:3rem;color:#a8aabd}.current-weather-container__current{grid-area:crnt;display:flex;justify-content:center;align-items:center;top:17.04%;font-family:"Roboto",sans-serif;font-style:normal;font-weight:normal;font-size:2.5rem;line-height:3rem;color:#a8aabd;font-size:14.81rem;line-height:15.38rem;font-weight:bold;color:#fff}.current-weather-container__info{grid-area:"inf";display:grid;grid-template-rows:3.5rem 16rem;grid-template-areas:"city" "date";grid-gap:2rem;gap:2rem}.current-weather-container__city{grid-area:city;font-family:"Roboto",sans-serif;font-style:normal;font-weight:normal;font-size:2.5rem;line-height:3rem;color:#a8aabd}.current-weather-container__date{font-family:"Roboto",sans-serif;font-style:normal;font-weight:normal;font-size:2.5rem;line-height:3rem;color:#a8aabd;grid-area:date;display:flex;flex-direction:column;justify-content:center;font-size:5.1rem;line-height:6.9rem;font-weight:bold;color:#fff}.current-weather-container__gap{grid-area:"spc"}@media all and (min-width: 481px) and (max-width: 1200px) and (max-height: 768px) and (orientation: landscape){.current-weather-container{grid-template-columns:3fr 4fr 5fr;grid-template-areas:" icn  tmp  inf ";grid-column-gap:3rem;-webkit-column-gap:3rem;column-gap:3rem;padding:0 1rem 0 3rem}.current-weather-container__icon{width:90%;height:auto}.current-weather-container__gap{display:none}}@media screen and (max-width: 480px) and (orientation: portrait){.current-weather-container{grid-template-columns:1fr 1fr 1fr;grid-column-gap:3rem;-webkit-column-gap:3rem;column-gap:3rem;grid-row-gap:3rem;row-gap:3rem;padding:0 1rem 0 3rem;grid-template-areas:"icn tmp tmp" "inf inf inf"}.current-weather-container__icon-container{grid-area:icn}.current-weather-container__temperature{grid-area:tmp}.current-weather-container__info{grid-area:inf;align-items:center;justify-content:center}.current-weather-container__gap{display:none}.current-weather-container__condition,.current-weather-container__max-min,.current-weather-container__city{font-size:5rem}}

.scroll-container{display:flex;flex-wrap:nowrap;overflow-x:auto;-webkit-overflow-scrolling:touch;-ms-overflow-style:none;scrollbar-width:none}.scroll-container::-webkit-scrollbar{display:none}.scroll-container .item{flex:0 0 auto;color:blue;padding:2px;scroll-snap-align:start}@media screen and (max-width: 480px) and (orientation: portrait){.scroll-container{align-self:flex-end}}

.card{position:relative;width:212px;height:400px;display:flex;flex-direction:column;justify-content:center;align-items:center;border-radius:6px}.card:hover{background:#3b3f69;cursor:pointer}.card:active,.card.active{background:#51557a}.card.glow{background-color:black;border:2px solid blue;border-radius:unset}.card__hour{width:100%;height:120px;display:flex;justify-content:center;align-items:center;font-family:"Roboto",sans-serif;font-style:normal;font-weight:normal;font-size:47.78px;line-height:56px;color:#a8aabd}.card__condition{display:flex;justify-content:center;align-items:center}.card__svg{fill:#ffc700;width:120px;height:auto}.card__temperature{width:100%;height:160px;font-family:"Roboto",sans-serif;font-style:normal;font-weight:bold;font-size:82.556px;line-height:96px;display:flex;align-items:center;justify-content:center;color:#fff}.card__cover{opacity:0%;position:absolute;left:0;top:0;width:100%;height:100%;background-color:#0e73aa;border:3px solid #2ab4ff;border-radius:unset;transition:opacity 300ms cubic-bezier(0.08, 0.73, 0, 0.71)}.card__cover.show{opacity:50%}@media screen and (max-width: 480px) and (orientation: portrait){.card{height:300px}.card__hour{font-size:10rem;line-height:11rem}.card__temperature{font-size:12rem;line-height:13rem}}@media screen and (min-width: 481px) and (max-width: 1400px){.card{height:350px}}

